function NotFoundPage() {
  let warning = "";

  let rand = Math.random();
  if (rand <= 0.33) {
    warning = " .";
  } else if (rand <= 0.66) {
    warning = "  .";
  } else {
    warning = "  .";
  }

  return (
    <div className="not-found-page">
      {warning}
      <br />
      404 Page Not Found
    </div>
  );
}

export default NotFoundPage;
